import React from 'react';

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/work/ourwork-aritaum-img@2x.jpg';
import prjImg1 from '../images/work/prj_img1.jpg';
import prjImg2 from '../images/work/prj_template1.jpg';

class Template1 extends React.Component {
    constructor(props){
        super(props);

        // json    
        this.idx = 7;
        this.state = {
            type: projectData[this.idx].type,        //project type
            cate: projectData[this.idx].cate,        //sorting category /    sorting
            title: projectData[this.idx].title,      //title
            desc: projectData[this.idx].desc,        //description
            mission: projectData[this.idx].mission,  //mission
            partner: projectData[this.idx].partner,  //partner
            launch: projectData[this.idx].launch,    //launch
            url: projectData[this.idx].url,          //url
            img:imgVisua1,                           //visual image
        }
    }

    render(){
        return (
            <React.Fragment>

                <div className="template">
                
                    <ViewTopCmp
                        type={this.state.type} 
                        title={this.state.title} 
                        desc={this.state.desc} 
                    />

                    <div className="inner_left">
                        <div className="visual">
                            <img src={this.state.img} alt=""/>
                        </div>
                    </div>

                    <div className="content inner_left">
                            
                        <div className="view_coltype">
                            <div className="left">
                                <p className="tit">challenge</p>
                            </div>
                            <div className="right">
                                <p className="desc">
                                     (simple   ) Clarifed()     
                                                
                                       .  Tone &amp; Manner     , 
                                        UX/UI        
                                        .
                                </p>
                                
                                <PrjInfoCmp 
                                    mission={this.state.mission}
                                    partner={this.state.partner}
                                    launch={this.state.launch}
                                    url={this.state.url}
                                />
                            </div>
                        </div>
                        
                    </div>

                    
                    <div className="thumb_set">
                        <img src={prjImg1} alt=""/>
                    </div>   

                    <div className="descbox">
                        <p>  </p>
                        <b>(One-Stop)  </b>
                        <p>      .  ,       ,   KEPCO     .              . </p>
                        <p>50      , , ,      . HTML5 CSS, ,  //     .                .</p>
                        <p>   CBD  ,        .       ,          .</p>
                    </div>

                    <div className="imgbox">
                        <img src={prjImg2} alt=""/>
                    </div>

                    <PrjBottomCmp/>

                </div>

            </React.Fragment>
        )
    }
}

export default Template1;