import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import AsianaPc from './AsianaPc';
import AsianaMo from './AsianaMo';

class Asiana extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    // 
    // cancel all subscriptions and asynchronous tasks in the componentWillUnmount method.
    //      PC 
    componentWillMount(){
        MobileCheck(this);
    }

    componentDidMount(){
        console.log('didMount');
    }

    componentWillUnmount(){
        console.log('willUnMount');
        //   ...
    }

    componentWillUpdate(){
        console.log('update');
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <AsianaMo /> : <AsianaPc />}
            </React.Fragment>
        )
    }
}

export default Asiana;