import img5 from '../images/project/mwave/m-img-txt.png';
import img6 from '../images/project/mwave/m-screen-01-1.png';
import img7 from '../images/project/mwave/m-screen-01-2.png';
import img8 from '../images/project/mwave/m-screen-01-3.png';
import img9 from '../images/project/mwave/m-screen-01-4.png';
import img10 from '../images/project/mwave/m-screen-01-5.png';
import img11 from '../images/project/mwave/m-screen-01-6.png';
import img12 from '../images/project/mwave/m-screen-02-1.png';
import img13 from '../images/project/mwave/m-screen-02-2.png';
import img14 from '../images/project/mwave/m-screen-02-3.png';
import img15 from '../images/project/mwave/m-screen-02-4.png';
