import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

import { ViewTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
import '../css/asiana.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/project/asiana/mo-asiana-logo.png';
import imgVisua2 from '../images/project/asiana/mo-asiana-top-object-pc.png';
import imgVisua3 from '../images/project/asiana/mo-asiana-top-object-m.png';
import imgVisua4 from '../images/project/asiana/mo-asiana-con-1-object.png';
import imgVisua5 from '../images/project/asiana/mo-asiana-con-1-icon.png';
import imgVisua6 from '../images/project/asiana/mo-asiana-con-2-object.png';
